.App {
  display: flex;
  padding: 16px;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  min-height: 100vh;
  background-color: rgb(234, 234, 234);
}

@media (min-width: 768px) {
  .App {
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    justify-content: center;
  }
}

.App-canvas-container {
  margin: 20px;
  width: 100%; /* Nutzt die maximale verfügbare Breite */
  height: auto; /* Höhe automatisch basierend auf dem Seitenverhältnis der Animation */
  max-width: 600px; /* Maximale Breite des Containers */
  aspect-ratio: 4 / 5;
}

.App-inputs-container {
  margin: 20px;
  max-width: 400px;
  width: 100%;
}

.input-group {
  margin-bottom: 20px;
}
